import { CST } from "../CST.js";
export class PlayScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENES.PLAY
        });

        this.stopwatchText = null;
        this.startTime = 0;
        this.elapsedTime = 0;
        this.gameEvents = [
            { time: 10, event: this.performEvent1.bind(this) },
            { time: 30, event: this.performEvent2.bind(this) },
            { time: 60, event: this.performEvent3.bind(this) },
        ];

        this.gameObjects = [
            { key: "Character_FlyingEye", texture: "FlyingEye.png" },
            { key: "Character_Goblin", texture: "Goblin.png" },
            { key: "Character_Mushroom", texture: "Mushroom.png" },
            { key: "Character_Skeleton", texture: "Skeleton.png" },
            { key: "Character_BringerOfDeath", texture: "BringerOfDeath.png" },
            { key: "Character_FrostGuardian", texture: "FrostGuardian.png" },
            { key: "Character_DemonSlime", texture: "DemonSlime.png" },
            // Add more game objects here
        ];
    }
    preload() {
        // Knight Attack
        this.anims.create({
            key: "KnightAttack",
            frameRate: 6,
            frames: this.anims.generateFrameNames("Characters", {
                prefix: "_KnightAttack_00",
                suffix: ".png",
                start: 0,
                end: 3
            }),
            delay: 200
        });
        // Knight Attack No Movement
        this.anims.create({
            key: "KnightAttackNoMovement",
            frameRate: 6,
            frames: this.anims.generateFrameNames("Characters", {
                prefix: "_KnightAttackNoMovement_00",
                suffix: ".png",
                start: 0,
                end: 3
            }),
            delay: 200
        });
        // Knight Attack Combo
        this.anims.create({
            key: "KnightAttackCombo",
            frameRate: 10,
            frames: this.anims.generateFrameNames("Characters", {
                prefix: "_KnightCombo_00",
                suffix: ".png",
                start: 0,
                end: 9
            }),
        });
        // Knight Idle
        this.anims.create({
            key: "KnightIdle",
            frameRate: 20,
            frames: this.anims.generateFrameNames("Characters", {
                prefix: "_KnightIdle_00",
                suffix: ".png",
                start: 0,
                end: 9
            }),
            repeat: -1
        });
        // Knight Run
        this.anims.create({
            key: "KnightRun",
            frameRate: 20,
            frames: this.anims.generateFrameNames("Characters", {
                prefix: "_KnightRun_00",
                suffix: ".png",
                start: 0,
                end: 9
            }),
            repeat: -1
        });
    }

    create() {
        
        this.textures.addSpriteSheetFromAtlas("Character_Knight", {frameWidth: 120, frameHeight: 80, atlas:"Simple_Characters_Enemy", frame: "Knight.png"});
        this.textures.addSpriteSheetFromAtlas("Character_FlyingEye", {frameWidth: 150, frameHeight: 150, atlas:"Simple_Characters_Enemy", frame: "FlyingEye.png"});
        this.textures.addSpriteSheetFromAtlas("Character_Goblin", {frameWidth: 150, frameHeight: 150, atlas:"Simple_Characters_Enemy", frame: "Goblin.png"});
        this.textures.addSpriteSheetFromAtlas("Character_Mushroom", {frameWidth: 150, frameHeight: 150, atlas:"Simple_Characters_Enemy", frame: "Mushroom.png"});
        this.textures.addSpriteSheetFromAtlas("Character_Skeleton", {frameWidth: 150, frameHeight: 150, atlas:"Simple_Characters_Enemy_2", frame: "Skeleton.png"});
        this.textures.addSpriteSheetFromAtlas("Character_BringerOfDeath", {frameWidth: 140, frameHeight: 93, atlas:"Simple_Characters_Boss_1", frame: "BringerOfDeath.png"});
        this.textures.addSpriteSheetFromAtlas("Character_DemonSlime", {frameWidth: 288, frameHeight: 160, atlas:"Simple_Characters_Boss_2", frame: "DemonSlime.png"});
        this.textures.addSpriteSheetFromAtlas("Character_FrostGuardian", {frameWidth: 192, frameHeight: 128, atlas:"Simple_Characters_Boss_3", frame: "FrostGuardian.png"});

        let Character_Knight = this.physics.add.sprite(400, 200, "Character_Knight").setScale(1.25);
        window.Character_Knight = Character_Knight;

        // Keyboard Input
        this.keyboard = this.input.keyboard.addKeys("W, A, S, D, J");

        this.stopwatchText = this.add.text(this.sys.game.config.width / 2, 10, "00:00:00", {fontFamily: "Monospace", fontSize: 24, color: "#ffffff"}).setOrigin(0.5, 0);
        
        // Start the stopwatch
        this.startStopwatch();

        // this.spawnGameObjects(2000, 60000);
    }

    
    // Spawner
    spawnGameObjects(spawnRate, endTime) {
        this.time.addEvent({
            delay: spawnRate,
            callback: () => {
                const randomIndex = Phaser.Math.Between(0, this.gameObjects.length - 1);
                const gameObject = this.gameObjects[randomIndex];
                const x = Phaser.Math.Between(0, this.sys.game.config.width);
                const y = Phaser.Math.Between(0, this.sys.game.config.height);
                this.physics.add.sprite(x, y, gameObject.key).setScale(1.25);
    
                // Check if the current time has exceeded the end time
                const currentTime = this.getTimeElapsed();
                if (currentTime >= endTime) {
                    this.time.removeEvent(this.spawnTimer);
                }
            },
            callbackScope: this,
            loop: true
        });
    }
    
    update() {
        

        // Knight Movement
        if(this.keyboard.W.isDown){
            Character_Knight.setVelocityY(-72);
            Character_Knight.anims.play("KnightRun", true);
        }else if(this.keyboard.A.isDown){
            Character_Knight.setFlipX(true);
            Character_Knight.setVelocityX(-72);
            Character_Knight.anims.play("KnightRun", true);
        }else if(this.keyboard.S.isDown){
            Character_Knight.setVelocityY(72);
            Character_Knight.anims.play("KnightRun", true);
        }else if(this.keyboard.D.isDown){
            Character_Knight.setFlipX(false);
            Character_Knight.setVelocityX(72);
            Character_Knight.anims.play("KnightRun", true);
        }else if(this.keyboard.J.isDown){
            Character_Knight.setVelocityX(0).setVelocityY(0);
            Character_Knight.anims.play("KnightAttackNoMovement", {
                onComplete: () => {
                    Character_Knight.anims.play("KnightIdle", true);
                }
            });
        }else{
            Character_Knight.setVelocityX(0).setVelocityY(0);
            Character_Knight.anims.play("KnightIdle", true);
        };


        // Timer
        const elapsedSeconds = Math.floor(this.elapsedTime / 1000);
        const hours = Math.floor(elapsedSeconds / 3600);
        const minutes = Math.floor((elapsedSeconds % 3600) / 60);
        const seconds = elapsedSeconds % 60;
        const formattedTime = `Time: ${this.padZero(hours)}:${this.padZero(minutes)}:${this.padZero(seconds)}`;
        this.stopwatchText.setText(formattedTime);
        
        // Perform game events based on the elapsed time
        this.gameEvents.forEach(event => {
            if (elapsedSeconds >= event.time && !event.triggered) {
                event.event();
                event.triggered = true;
            }
        });

        this.elapsedTime = this.getTimeElapsed();
    }

    startStopwatch() {
        this.startTime = this.getTimeElapsed();
    }
    
    getTimeElapsed() {
        return new Date().getTime() - this.startTime;
    }
    
    padZero(number) {
        return number.toString().padStart(2, "0");
    }
    
    performEvent1() {
        console.log("Performing Event 1");
    }
    
    performEvent2() {
        console.log("Performing Event 2");
    }
    
    performEvent3() {
        console.log("Performing Event 3");
    }
    
}